img {
    height: auto;
    width: 100%;
    max-width: 150px;
    border: 1px;
    border-radius: 10px;
}

p {
    text-align: center;
}

.description {
    padding-top: 15px;
    height: 80px;
}

.grid-container {
    padding: 40px;
    border-radius: 10px;
}

.content-container {
    display: flex;
    justify-content: center;
    margin: 5px;
}

.button-container {
    display: flex;
    flex-direction: column;
    margin: 20px;
    padding-left: 4px;
}

.image-container {
    display: flex;
    justify-content: center;
}

.column-item {
    position: relative !important;
    top: 0 !important;
    border: 1px solid lightgray;
    border-radius: 15px;
    min-width: 190px;
    max-height: 400px !important;
    transition: background 1s, top 0.3s !important;
}

.column-item:hover {
    background: rgb(240,219,79,0.7) !important;
    top: -10px !important;
}